import axios from "axios"
export default {
  data() {
    const userName = window.localStorage.getItem("userName")
    const friendNick = window.localStorage.getItem("friendNick")
    const myId = this.$route.params.id
    return {
      userName,
      myId,
      friendNick,
      hasPic: false
    }
  },
	mounted() {
    if(this.myId!==null){
      axios
        .get("http://121.42.177.97:8080/red/use/pic_id.do?user_id=" + this.myId)
        .then(res => {
          this.hasPic = true
        })
        .catch(res => {
          throw res
        })
    }
  }
}



